@import 'node_modules/@glidejs/glide/src/assets/sass/glide.core';
@import 'node_modules/@glidejs/glide/src/assets/sass/glide.theme';

img {
  display: block;
  max-width: 100%;
  height: auto;
}
a, li {
  text-decoration: none;
  list-style: none;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
  padding: 0;
}

.container{
  width: 1024px;
}

.slide__title {
    margin-top: 25px;
  
    text-align: center;
    font-weight: 700;
    font-size: 20px;
    line-height: 1.17;
    text-transform: uppercase;
    text-shadow: 4px 4px 2px rgba(150, 150, 150, 1);
  }
  
  .cards__image-poster {
    margin-top: 10px;
    width: 100px;
    height: 100px;
    object-fit: cover;
    border-radius: 5px;
    transition: box-shadow 250ms cubic-bezier(0.4, 0, 0.2, 1);
    &:hover,
    &:focus {
      box-shadow: 0px 0px 5px 0px #ffffff;
    }
  }
  
  .glide__slide--main {
    padding-top: 20px;
    padding-bottom: 30px;
    transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
    &:hover,
    &:focus {
      transform: scale(1.2);
      border-radius: 5px;
    }
  }

  